import React from "react";
import HeaderMatrices from "./HeaderMatrices";
import KeyboardArrowRightIcon from "@material-ui/icons/KeyboardArrowRight";
import ThumbUpAltIcon from "@material-ui/icons/ThumbUpAlt";
import EditIcon from "@material-ui/icons/Edit";
export default function Matrices() {
  return (
    <div>
      <HeaderMatrices />
      <div className="MatricesMainComponent">
        <section className="days">
          <div className="header">
            <h5>Your Bussiness</h5>
          </div>

          <div className="box1">
            <ul>
              <li>
                Today
                <KeyboardArrowRightIcon />
              </li>
              <li>
                Yesterday
                <KeyboardArrowRightIcon />
              </li>
              <li>
                Sunday
                <KeyboardArrowRightIcon />
              </li>
              <li>
                Saturday
                <KeyboardArrowRightIcon />
              </li>
              <li>
                Friday
                <KeyboardArrowRightIcon />
              </li>
              <li>
                Thrusday
                <KeyboardArrowRightIcon />
              </li>
              <li>
                Wednesday
                <KeyboardArrowRightIcon />
              </li>
            </ul>
          </div>
        </section>
        <section className="graph">
          <div className="top-insight">
            <div className="top-section">Tue, 30 Apr</div>
            <div className=" grid-container">
              <div className="left-side">
                <span>Top Insights</span>
                <br />
                <span id="large-font">21</span>/25 <br />
                <span id="small-font">GOOD FOOF READY</span>
              </div>
              <div className="right-side">
                <div className="pic">
                  <div className="filled"></div>
                </div>
              </div>
            </div>
          </div>
          <section className="operationalMatrices">
            <div className="heading">
              <h6>Operational Metrices</h6>
            </div>
            <div className="box3">
              <div className="left">
                <h3>100%</h3>
                <small>Accepted Orders</small>
              </div>
              <div className="right">
                <ThumbUpAltIcon fontSize="inherit" />
              </div>
            </div>
          </section>
        </section>
        <section className="breakdown">
          <section className="top"></section>

          <section className="extraSpace">
            <div className="left">
              <h3>23%</h3>
              <small>Edit Orders</small>
            </div>
            <div className="right">
              <EditIcon fontSize="inherit" />
            </div>
          </section>
        </section>
      </div>
    </div>
  );
}
